@use '../base/settings' as *;
.header {
    position: relative; 
    display: flex; 
    overflow: hidden; 
}
.header__bg {
    position: absolute; 
    top: 0;
    left: 0;
    width: 100%; 
    height: 100%; 
    object-fit: cover; 
    z-index: -1; 
}
.header h1 {
    background-image: linear-gradient(to right, #ffffff, #0044ff, #ffffff, #0044ff);
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
    animation: appear 1s linear forwards alternate;
    font-size: 7rem;
    padding: $m-size;
    text-align: center;
    padding-left: 10%;
}

@keyframes appear {
    0% {
        transform: scale(0)
    }
    100% {
        transform: scale(1)
    }
}

.header__container {
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: visible;
    justify-content: space-between;
    width:100vw;
}

.header ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #000000;
    display: flex;
    justify-content: center;
    margin-right: 10%;
}

.header li {
    border: 1px solid #bbb;
    border: 1px solid #bbb;
}

.header li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 2rem;
    transition-duration: 0.5s;
}

.header li a:hover {
    background-color: #0044ff;
}

.active {
    background-color: #04AA6D;
}

@media(max-width: $mobile-version) {
  .header__container{
    justify-content: left;
  }

 
}

@media(max-width: $header-break) {
    .header__container{
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .header ul {
        margin:0;
        margin-top:4rem;
        margin-bottom:3rem;
    }
    .header h1 {
        padding:0;
    }
  
  }

