@use '../base/settings' as *;

.book-banner {
    position:relative;
    margin: $m-size $xl-size;
    padding-top: $m-size;
    overflow:hidden;   
}

.book-banner p  {
    max-width: $img-size;
    text-wrap: wrap;
    text-align: center;
    color: $white;
    font-size: $m-size;
    height: auto;
    
}

.book-banner a{
    text-decoration: none;
}

.book-banner img {
    display:block;
    transition: all 0.5s;
    border-radius: 5%;
    max-width: $img-size;
    height: auto;
    border: 1px solid black;
}

.book-banner:hover img{
    transform: scale(1.05);
    border-radius: 15%;
}



@media(max-width: $mobile-version) {
    
    .book-banner img {
        max-width: $img-size;
        height: auto;
        border: 1px solid black;

}
}
